body {
	--primary: #49a2c4;
	--secondary: #8eb2c0;
	--background: #f1f1f1;
	--text: #49a2c4;
	--button-text: #fff;
	--red: #f8c7c3;
	--orange: #f8d8bf;
	--yellow: #f0e794;
	--green: #b9ebc8;
	--violet: #d4caf7;
}

body.admin-color-light {
	--primary: #04a4cc;
	--secondary: #e5e5e5;
	--text: #999;
}

body.admin-color-modern {
	--primary: #3858e9;
	--text: #1e1e1e;
}

body.admin-color-blue {
	--primary: #4796b3;
	--secondary: #74B6CE;
	--text: #096484;
}

body.admin-color-coffee {
	--primary: #c7a589;
	--secondary: #9ea476;
	--text: #46403c;
}

body.admin-color-ectoplasm {
	--primary: #a3b745;
	--secondary: #a3b745;
	--text: #523f6d;
}

body.admin-color-midnight {
	--primary: #e14d43;
	--secondary: #363b3f;
	--text: #25282b;
}

body.admin-color-ocean {
	--primary: #9ebaa0;
	--secondary: #738e96;
	--text: #627c83;
}

body.admin-color-sunrise {
	--primary: #dd823b;
	--secondary: #e5e5e5;
	--text: #000;
}

.tumblr-themes {
	display: grid;
	grid-template-columns: 95%;
	justify-content: center;
	width: 98%;
	margin: 0 auto;
}

.tumblr-theme {
	width: 100%;
	color: var(--text);
	margin-bottom: 40px;
	font-family: monospace;
	filter: drop-shadow(-8px 8px 0 var(--red));

	&:nth-child(5n + 2) {
		filter: drop-shadow(-8px 8px 0 var(--orange));
	}

	&:nth-child(5n + 3) {
		filter: drop-shadow(-8px 8px 0 var(--yellow));
	}

	&:nth-child(5n + 4) {
		filter: drop-shadow(-8px 8px 0 var(--green));
	}

	&:nth-child(5n + 5) {
		filter: drop-shadow(-8px 8px 0 var(--violet));
	}
}

@media screen and (min-width: 1301px) {
	.tumblr-themes  {
		grid-template-columns: 30% 30% 30%;
		justify-content: space-between;
	}
}

@media screen and (min-width: 730px) and (max-width: 1300px) {
	.tumblr-themes  {
		grid-template-columns: 47% 47%;
		justify-content: space-between;
	}
}

.tumblr-theme-header {
	display: flex;
	align-items: flex-start;
	overflow: hidden;
	position: relative;
	margin-left: -8px;
	width: calc(100% + 8px);
	height: 26px;
}

.tumblr-theme-title-wrapper {
	flex: initial;
	display: block;
	cursor: pointer;
	position: relative;
	margin-left: 4px;
	padding: 4px 4px 0 4px;
}

.tumblr-theme-content {
	background-color: var(--background);
	padding: 10px;
	box-shadow:
		-2px -2px 0 0 var(--background),
		0 -2px 0 0 var(--background),
		-2px 0 0 0 var(--background),
		0 2px 0 0 var(--background),
		2px 0 0 0 var(--background),
		0 0 0 2px var(--text),
		-4px -4px 0 0 var(--text),
		0 -4px 0 0 var(--text),
		-4px 0 0 0 var(--text),
		0 4px 0 0 var(--text),
		4px 0 0 0 var(--text);
}

.tumblr-theme-thumbnail {
	width: calc(100% - 4px);
	border: 2px solid var(--secondary);
	border-radius: 4px;
}

.tumblr-theme-title {
	display: block;
	position: relative;
	background-color: var(--background);
	padding: 3px 12px 0 10px  ;
	height: 18px;
	line-height: 18px;
	font-family: monospace;
	font-size: 1em;
	font-weight: bold;
	box-shadow:
		-2px 4px 0 var(--background),
		0 -2px 0 var(--background),
		-2px 0 0 var(--background),
		2px 0 0 var(--background),
		4px 2px 0 var(--background),
		6px 4px 0 var(--background),
		8px 6px 0 var(--background),
		10px 8px 0 var(--background),
		12px 10px 0 var(--background),
		14px 12px 0 var(--background),
		16px 14px 0 var(--background),
		18px 16px 0 var(--background),
		20px 18px 0 var(--background),
		-4px 0 0 var(--text),
		0 -4px 0 var(--text),
		-2px -2px 0 var(--text),
		2px -2px 0 var(--text),
		4px 0 0 var(--text),
		6px 2px 0 var(--text),
		8px 4px 0 var(--text),
		10px 6px 0 var(--text),
		12px 8px 0 var(--text),
		14px 10px 0 var(--text),
		16px 12px 0 var(--text),
		18px 14px 0 var(--text),
		20px 16px 0 var(--text)
}

.tumblr-theme-buttons {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	position: relative;
	margin: 10px 0  0;
	padding: 0;
	width: 100%;
}

.tumblr-theme-buttons li {
	flex: none;
	display: block;
	overflow: hidden;
	position: relative;
	margin-left: 10px;
	border-radius: 4px;
}


.tumblr-theme-buttons li a {
	display: block;
	overflow: hidden;
	padding: 0 12px;
	line-height: 24px;
	font-size: 1em;
	white-space: nowrap;
	text-overflow: ellipsis;
	text-decoration: none;
	border-radius: inherit;
	color: var(--button-text);
	background: var(--primary);
}
